// initial state
const state = {
  UserInfo: {
    id: '1',
    phone: 13100609928,
    nickname: 'wwj',
    logo: "https://jxsr-oss1.antelopecloud.cn/files?obj_id=5c0740bb8000400c04302c28&access_token=2147500044_3356491776_1576080023_4197ff5c2ea3c5abf3c2b8d6f0e931fd",
    tel: '400-401-400'
  }
}

// getters
const getters = {}

// actions
const actions = {
  getUserInfo({ commit }) {
    commit('setProducts', [1,2,3])
  }
}

// mutations
const mutations = {
  setProducts(state, products) {
    state.all = products
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


