export default [
  {
    path: '/',
    name: 'index',
    component: resolve => require(['@/view/Index'], resolve)
  },
  {
    path: '/course',
    name: 'course',
    component: resolve => require(['@/view/course'], resolve)
  },
  {
    path: '/system',
    name: 'system',
    component: resolve => require(['@/view/system'], resolve)
  },
  {
    path: '*',
    name: 'error',
    component: resolve => require(['@/view/404'], resolve)
  }
]


